
.action {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 2em;
}
.action div {
    text-align: center;
    margin-bottom: 20px;
    width: 100%;
}
.action .signin-button {
    background: #00BAFF;
    border-radius: 30px;
    border: 15px solid #00BAFF;
    color: #ffffff;
    display: block;
    font-family: sans-serif;
    font-size: 17px;
    font-weight: 700;
    line-height: 1.1;
    text-align: center;
    text-decoration: none;
    width: 100%;
}
.action .fb {
    background: #4267B2;
    border: 15px solid #4267B2;
}
.alexa-login-footer-padding {
  margin-top: 60px;
}
.footer {
  text-align: center;
  font-size: 12px;
  font-weight: normal;
}
.footer a {
    text-decoration: underline;
}
.footer .address {
    overflow-wrap: normal;
    padding-left: 2em;
    padding-right: 2em;
    word-wrap: break-word;
}
.footer .company {
    color: #353434;
    font-weight: 700;
}
.footer .terms {
    color: #00BAFF;
}
.login-form {
  font-size: 15px;
  font-weight: 700;
  margin-top: 40px;
  padding: 1.2em 0.8em;
  text-align: left;
}
.login-form .email-password-fields {
    margin-left: 1.5em;
    margin-right: 3em;
}
.login-form .email-password-fields div:first-child {
      padding-bottom: 3em;
}
.login-form .email-password-fields input {
      border-bottom: 2px #d2d2d2 solid;
      border-left: 0px;
      border-radius: 0px;
      border-right: 0px;
      border-top: 0px;
      box-shadow: unset;
      font-size: 11px;
      font-weight: 400;
      height: 2em;
      margin: auto;
      padding-left: 0;
      padding-right: 0;
      width: 100%;
}
.login-form .email-password-fields p {
      margin-bottom: .5em;
}
.logo img {
  margin-bottom: 10px;
}
.logo h1 {
  color: #00BAFF;
  font-size: 30px;
  font-weight: 700;
  margin-bottom: 20px;
  margin-top: 20px;
}
.error-message {
  background: #f3f3f3f3;
  border-radius: 5px;
  margin-bottom: 20px;
  margin-top: 10px;
  padding: 10px;
}
.error-message .error-title {
    font-size: 17.55px;
    font-weight: 700;
    margin: 0px;
    padding-bottom: 0px;
}
.error-message .error-content {
    font-weight: 500;
}



